.hero {
    width: 100%;
    height: 100vh;
    color: white;
    position: relative;
    z-index: -1;
    background-image: url("../images/background.jpg");
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size: 100% 100%;
}

/* .background-image {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
} */

.hero h1 {
    margin-bottom: 0;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 5vw;
    text-align: center;
    line-height: 100%;
}

.hero h2 {
    font-weight: 500;
    font-size: 1.8vw;
    text-transform: uppercase;
    text-align: center;
}

/* Media Queries */
@media screen and (max-width: 800px) {
    .hero h2 {
        font-size: 2vw;
    }
}
